import { type AppConfig, type AppProps, PROXY_URI } from '@td-org/chat-widget-shared'

export function buildSnippetCode(props: AppProps, configs: AppConfig): string {
  let stylesString = ''
  const proxyHost = props.proxyHost || ''
  try {
    const theme: Record<string, string> = { ...(configs?.styles || {}) }
    Object.keys(theme).forEach((key) => {
      if (theme[key] === '' || theme[key] === null || theme[key] === undefined) {
        delete theme[key]
      }
    })
    if (Object.keys(theme).length > 0) {
      stylesString = JSON.stringify(theme)
    }
  } catch (e) {
    stylesString = ''
  }
  const propsList = [
    `touchpointId: "${props.touchpointId || 'please replace your touchpointId'}"`,
    'accountId: ""',
    `region: "${props.region || 'please replace your td region'}"`
  ]
  const configsList = [
    `enableEmoji: ${configs.enableEmoji ?? true}`,
    `enableAttachments: ${configs.enableAttachments ?? true}`,
    `enableValidation: ${configs.enableValidation ?? false}`,
    `enableResponsiveLayout: ${configs.enableResponsiveLayout ?? true}`,
    `"enablePointMoveTriggerButton": ${configs.enablePointMoveTriggerButton ?? false}`,
    `enableUserInput: ${configs.enableUserInput ?? true}`,
    `languageCode: "${configs.languageCode || ''}"`
  ]
  if (proxyHost) {
    propsList.push(`proxyHost: "${proxyHost}"`)
  }
  if (stylesString) {
    configsList.push(`styles: ${stylesString}`)
  }
  return `<!-- Start of Talkdesk Code -->
    <script>
      var webchat;
      (function(window, document, node, props, configs) {
        if (window.TalkdeskChatSDK) {
          console.error("TalkdeskChatSDK already included");
          return;
        }
        var divContainer = document.createElement("div");
        divContainer.id = node;
        document.body.appendChild(divContainer);
        var src = "${
          proxyHost ? `https://${proxyHost}${PROXY_URI.tdChatSdk}` : import.meta.env.VITE_CDN_BASE
        }/v2/talkdeskchatsdk.js";
        var script = document.createElement("script");
        var firstScriptTag = document.getElementsByTagName("script")[0];
        script.type = "text/javascript";
        script.charset = "UTF-8";
        script.id = "tdwebchatscript";
        script.src = src;
        script.async = true;
        firstScriptTag.parentNode.insertBefore(script, firstScriptTag);
        script.onload = function() {
          webchat = TalkdeskChatSDK(node, props);
          webchat.init(configs);
          /*
           * Send custom data from your website to TalkDesk!
           * If you would like to do it, you need to remove the following commented code and
           * modify the webchat.setContextParam parameters to pass in the data you need.
           */
           /*function setContext() {
             webchat.setContextParam({ "var1": "value1", "var2": "value2", "var3": 100 })
           }
           // Send data when the chat conversation is initiated
           webchat.onConversationStart = function() {
             setContext()
           }
           // Send data when the chat widget is open
           webchat.onOpenWebchat = function() {
             setContext()
           }*/
        };
      })(
        window,
        document,
        "tdWebchat",
        { ${propsList.join(', ')} },
        { ${configsList.join(', ')} }
      );
    </script>
<!-- End of Talkdesk Code -->`
}
