import { useCallback, useEffect, useMemo, useRef, useState } from 'react'

import { DEFAULT_PREVIEW_CONFIG_TEXT, DEFAULT_THEME, type Theme } from '@td-org/chat-widget-shared'

import ConfigurationForm from './components/ConfigurationForm'
import { getQueryParam } from './utils/getQueryParam'
import { initChat, initPreview, type PreviewSDK, type SDK } from './utils/initChat'
import { getRegion, Region } from './utils/region'

const defaultContext = `{
  "field_name1": "David"
}`

const replaceOptions = [
  { key: 'touchpointId', params: 'touchpoint_id', type: 'string' },
  { key: 'region', params: 'region', type: 'string' },
  { key: 'proxyHost', params: 'proxy_host', type: 'string' },
  { key: 'lang', params: 'lang', type: 'string' },
  { key: 'enableEmoji', params: 'enable_emoji', type: 'boolean' },
  { key: 'enableAttachments', params: 'enable_attachments', type: 'boolean' },
  { key: 'enableValidation', params: 'enable_validation', type: 'boolean' },
  { key: 'enablePreview', params: 'enable_preview', type: 'boolean' },
  { key: 'enablePointMoveTriggerButton', params: 'enable_point_move_trigger_button', type: 'boolean' }
]

function setDefaultByBooleanQueryParams(key: string, defaultValue: boolean) {
  const value = getQueryParam(key)
  if (value === '1') return true
  if (value === '0') return false
  return defaultValue
}

function Playground() {
  const sdkRef = useRef<SDK>()
  const previewSDKRef = useRef<PreviewSDK>()
  const isUpdateConfigs = useRef<boolean>(false)
  const loading = useRef<boolean>(false)
  const [touchpointId, setTouchpointId] = useState(getQueryParam('touchpoint_id') || '')
  const [region, setRegion] = useState<Region>(getRegion(getQueryParam('region')) || 'td-us-1')
  const [lang, setLang] = useState(getQueryParam('lang') || '')
  const [enableEmoji, setEnableEmoji] = useState<boolean>(setDefaultByBooleanQueryParams('enable_emoji', true))
  const [enableAttachments, setEnableAttachments] = useState<boolean>(
    setDefaultByBooleanQueryParams('enable_attachments', true)
  )
  const [enableValidation, setEnableValidation] = useState<boolean>(
    setDefaultByBooleanQueryParams('enable_validation', true)
  )
  const [enableAutoOpen, setEnableAutoOpen] = useState<boolean>(
    setDefaultByBooleanQueryParams('enable_auto_open', false)
  )
  const [enableResponsiveLayout, setEnableResponsiveLayout] = useState<boolean>(
    setDefaultByBooleanQueryParams('enable_responsive_layout', true)
  )
  const [enablePointMoveTriggerButton, setEnablePointMoveTriggerButton] = useState<boolean>(
    setDefaultByBooleanQueryParams('enable_point_move_trigger_button', false)
  )
  const [enableUserInput, setEnableUserInput] = useState<boolean>(
    setDefaultByBooleanQueryParams('enable_user_input', true)
  )
  const [enablePreview, setEnablePreview] = useState<boolean>(setDefaultByBooleanQueryParams('enable_preview', false))
  const [context, setContext] = useState(defaultContext)
  const [proxyHost, setProxyHost] = useState<string>(getQueryParam('proxy_host') || '')
  const [previewConfig, setPreviewConfig] = useState(DEFAULT_PREVIEW_CONFIG_TEXT)
  const [events, setEvents] = useState<Array<{ name: string; timestamp: Date }>>([])
  const [styles, setStyles] = useState<Partial<Theme>>({})
  const [updateTime, setUpdateTime] = useState(Date.now())
  const sdkPayload = useMemo(
    () => ({
      proxyHost,
      touchpointId,
      region,
      languageCode: lang,
      enableEmoji,
      enableAttachments,
      enableValidation,
      enableAutoOpen,
      enableResponsiveLayout,
      enablePointMoveTriggerButton,
      enableUserInput,
      context,
      previewConfig,
      styles,
      enablePreview,
      onEvent: (event: string) => setEvents((prev) => [...prev, { name: event, timestamp: new Date() }]),
      _updateTime: updateTime
    }),
    [
      proxyHost,
      touchpointId,
      region,
      lang,
      enableEmoji,
      enableAttachments,
      enableValidation,
      enableAutoOpen,
      enableResponsiveLayout,
      enablePointMoveTriggerButton,
      enableUserInput,
      context,
      previewConfig,
      styles,
      enablePreview,
      updateTime
    ]
  )

  const handleUpdated = useCallback(
    (
      data: {
        proxyHost: string
        touchpointId: string
        region: Region
        lang: string
        enableEmoji: boolean
        enableAttachments: boolean
        enableAutoOpen: boolean
        enableValidation: boolean
        enableResponsiveLayout: boolean
        enablePointMoveTriggerButton: boolean
        enableUserInput: boolean
        enablePreview: boolean
        context: string
        previewConfig: string
        styles: Partial<Theme>
      },
      isAppConfigs: boolean
    ) => {
      const url = replaceOptions
        .reduce((acc, { key, params, type }) => {
          const value = data[key as keyof typeof data]
          if (type === 'boolean') {
            acc = [...acc, `${params}=${value ? '1' : '0'}`]
          } else if (type === 'string' && value) {
            acc = [...acc, `${params}=${value}`]
          }
          return acc
        }, [] as string[])
        .join('&')
      history.replaceState(null, '', `?${url}`)

      isUpdateConfigs.current = isAppConfigs

      setProxyHost(data.proxyHost)
      setTouchpointId(data.touchpointId)
      setRegion(data.region)
      setLang(data.lang)
      setEnableEmoji(data.enableEmoji)
      setEnableAttachments(data.enableAttachments)
      setEnableValidation(data.enableValidation)
      setEnableAutoOpen(data.enableAutoOpen)
      setEnableResponsiveLayout(data.enableResponsiveLayout)
      setEnablePointMoveTriggerButton(data.enablePointMoveTriggerButton)
      setEnableUserInput(data.enableUserInput)
      setEnablePreview(data.enablePreview)
      setContext(data.context)
      setPreviewConfig(data.previewConfig)
      setStyles(data.styles)
      setUpdateTime(Date.now())
    },
    []
  )

  const handleUpdatePreviewConfig = useCallback((config: string) => {
    if (previewSDKRef.current) {
      previewSDKRef.current.updateConfig(JSON.parse(config))
    }
  }, [])

  const handleOnUpdateContext = useCallback((context: string) => {
    if (sdkRef.current) {
      try {
        sdkRef.current.setContextParam(JSON.parse(context))
      } catch (e) {
        console.warn(`Failed to update context = ${context}`)
      }
    }
  }, [])

  const handleDestroyed = useCallback(() => {
    if (sdkRef.current) {
      sdkRef.current.destroy()
      sdkRef.current = undefined
      loading.current = false
    }
    if (previewSDKRef.current) {
      previewSDKRef.current.destroy()
      previewSDKRef.current = undefined
      loading.current = false
    }
    isUpdateConfigs.current = false
  }, [])

  useEffect(() => {
    if (sdkPayload.enablePreview) {
      if (!previewSDKRef.current && !loading.current) {
        loading.current = true
        initPreview(sdkPayload)
          .then((preview) => {
            previewSDKRef.current = preview
          })
          .catch((error: unknown) => {
            console.error(error)
          })
          .finally(() => {
            loading.current = false
          })
      }
    }

    return () => {
      previewSDKRef.current?.destroy()
      previewSDKRef.current = undefined
    }
  }, [sdkPayload])

  useEffect(() => {
    if (!sdkPayload.enablePreview) {
      if (!sdkRef.current && !loading.current && sdkPayload.touchpointId && sdkPayload.region) {
        loading.current = true
        initChat(sdkPayload)
          .then((chat) => {
            sdkRef.current = chat
          })
          .catch((error: unknown) => {
            console.error(error)
          })
          .finally(() => {
            loading.current = false
          })
      }
      if (isUpdateConfigs.current && sdkRef.current && !loading.current) {
        sdkRef.current.init({
          enableEmoji: sdkPayload.enableEmoji,
          enableAttachments: sdkPayload.enableAttachments,
          enableValidation: sdkPayload.enableValidation,
          enableResponsiveLayout: sdkPayload.enableResponsiveLayout,
          enableUserInput: sdkPayload.enableUserInput,
          languageCode: sdkPayload.languageCode,
          styles: sdkPayload.styles
        })
      }
    }

    return () => {
      if (!isUpdateConfigs.current) {
        sdkRef.current?.destroy()
        sdkRef.current = undefined
      }
    }
  }, [sdkPayload])

  return (
    <div id='widget'>
      <ConfigurationForm
        proxyHost={proxyHost}
        touchpointId={touchpointId}
        region={region}
        lang={lang}
        enableEmoji={enableEmoji}
        enableAttachments={enableAttachments}
        enableValidation={enableValidation}
        enableAutoOpen={enableAutoOpen}
        enableResponsiveLayout={enableResponsiveLayout}
        enablePointMoveTriggerButton={enablePointMoveTriggerButton}
        enableUserInput={enableUserInput}
        enablePreview={enablePreview}
        onUpdate={handleUpdated}
        onDestroy={handleDestroyed}
        defaultTheme={DEFAULT_THEME}
        defaultContext={defaultContext}
        defaultPreviewConfig={DEFAULT_PREVIEW_CONFIG_TEXT}
        events={events}
        onClearEvents={() => setEvents([])}
        onUpdatePreviewConfig={handleUpdatePreviewConfig}
        onUpdateContext={handleOnUpdateContext}
      />
      {enablePreview && (
        <div className='preview-container'>
          <div id='preview'></div>
        </div>
      )}
    </div>
  )
}

export default Playground
