import { type ReactNode, useEffect, useLayoutEffect, useRef, useState } from 'react'
import clsx from 'clsx'

import { Icon } from './Icon'

import classes from './Section.module.scss'

type SectionProps = {
  title: string
  children: ReactNode
  defaultExpand?: boolean
  onStickyTop?: (isSticky: boolean) => void
}

export const Section = (props: SectionProps): JSX.Element => {
  const { title, children, defaultExpand = true, onStickyTop } = props
  const [expand, setExpand] = useState(defaultExpand)
  const ref = useRef<HTMLDivElement>(null)

  useEffect(() => {
    setExpand(defaultExpand)
  }, [defaultExpand])

  useLayoutEffect(() => {
    if (!ref.current) {
      return
    }

    const cachedRef = ref.current
    const observer = new IntersectionObserver(
      ([e]) => {
        onStickyTop?.(e.intersectionRatio < 1)
      },
      { threshold: [1] }
    )

    observer.observe(cachedRef)

    return () => {
      observer.unobserve(cachedRef)
    }
  }, [onStickyTop])

  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    event.preventDefault()
    event.stopPropagation()
    setExpand((prev) => !prev)
  }

  return (
    <>
      <div className={classes.container} ref={ref}>
        <div className={clsx(classes.title, { [classes.open]: expand })}>
          <button className={classes.button} onClick={handleClick}>
            <h3>{title}</h3>
            <Icon />
          </button>
        </div>
        <div className={classes.divider} />
      </div>
      <div className={classes.section} style={{ opacity: expand ? 1 : 0 }}>
        {expand && children}
      </div>
    </>
  )
}
