import { type Theme } from '@td-org/chat-widget-shared'

import { getValue } from '../../utils/helper'

import classes from './StyleInput.module.scss'

type ThemeKey = keyof Theme

type StyleInputProps = {
  styleKey: ThemeKey
  value: string
  defaultValue?: string
  onChange: (styleKey: ThemeKey, value: string) => void
}

export const StyleInput = (props: StyleInputProps): JSX.Element => {
  const { styleKey, value, defaultValue, onChange } = props

  const isChanged = !!(value && value !== defaultValue)

  const isColorSelect = (value?: string): boolean => {
    return !!value && value.startsWith('#') && value.length === 7
  }

  return (
    <div className={classes.input}>
      <div className={classes.content}>
        <input
          id={styleKey}
          value={value}
          onChange={(e) => onChange(styleKey, getValue(e))}
          placeholder={defaultValue}
          className={isChanged ? classes.changed : ''}
        ></input>
      </div>
      <div className={classes.helper}>
        {isColorSelect(defaultValue) && (
          <input type='color' value={value || defaultValue} onChange={(e) => onChange(styleKey, getValue(e))}></input>
        )}
      </div>
    </div>
  )
}
