import { ChangeEvent } from 'react'

export function getValue(event: ChangeEvent<HTMLInputElement>): string {
  return event.target.value
}

export function getChecked(event: ChangeEvent<HTMLInputElement>): boolean {
  return event.target.checked
}

export function convertSearchString(search: string, delimiter: string = ','): string[] {
  if (!search.includes(delimiter)) {
    return [search]
  }

  return search
    .split(delimiter)
    .map((s) => s.trim())
    .filter((s) => s.length > 0)
}
