import formatter from '../../utils/dateTimeFormatter'

import classes from './Output.module.scss'

type OutputProps = {
  events: Array<{ name: string; timestamp: Date }>
  onClearEvents: () => void
}

function fullDateTime(date: Date): string {
  return `${date.toLocaleDateString()} ${date.toLocaleTimeString()}`
}

export function Output(props: OutputProps): JSX.Element {
  const { events, onClearEvents } = props

  return (
    <div className={classes.container}>
      <div className={classes.actions}>
        <button onClick={onClearEvents}>Clear events</button>
      </div>
      <div className={classes.events}>
        {events.length === 0 && <span>No events yet</span>}
        {events.map((event) => {
          return (
            <div className={classes.event} key={event.timestamp.getTime()}>
              <span className={classes.time} title={fullDateTime(event.timestamp)}>
                {formatter.format(event.timestamp)}
              </span>
              <span className={classes.content}>{event.name}</span>
            </div>
          )
        })}
      </div>
    </div>
  )
}
