import { convertSearchString } from '../../utils/helper'

import classes from './Highlight.module.scss'

type HighlightProps = {
  highlight?: string
  text: string
}

export const Highlight = (props: HighlightProps): JSX.Element => {
  const { highlight, text } = props

  if (!highlight) {
    return <>{text}</>
  }

  const highlightSegments = convertSearchString(highlight).join('|')

  const segments = text.split(new RegExp(`(${highlightSegments})`, 'gi'))

  const isMatch = (segment: string) => highlight.toLowerCase().includes(segment.toLowerCase())

  return (
    <>
      {segments.map((segment, index) =>
        isMatch(segment) ? (
          <mark key={index} className={classes.mark}>
            {segment}
          </mark>
        ) : (
          segment
        )
      )}
    </>
  )
}
