import type TalkdeskChatSDK from '@td-org/chat-widget-sdk'
import type { PreviewSDK, SDK } from '@td-org/chat-widget-sdk'
import { PROXY_URI } from '@td-org/chat-widget-shared'

type InitFn = SDK['init']
type Region = Parameters<typeof TalkdeskChatSDK>[1]['region']
export type Theme = NonNullable<NonNullable<Parameters<InitFn>[0]>['styles']>

export type { SDK, PreviewSDK }

let currentRemoteSdk: typeof TalkdeskChatSDK | null = null

function loadRemoteSdk(url: string) {
  return new Promise<typeof TalkdeskChatSDK | null>((resolve) => {
    if (currentRemoteSdk) {
      resolve(currentRemoteSdk)
    } else {
      try {
        const script = document.createElement('script')
        script.src = url
        script.async = true
        document.body.appendChild(script)
        script.onload = () => {
          // eslint-disable-next-line @typescript-eslint/no-explicit-any
          currentRemoteSdk = (window as any).TalkdeskChatSDK
          resolve(currentRemoteSdk)
        }
        script.onerror = () => {
          resolve(currentRemoteSdk)
        }
      } catch (e) {
        console.warn(`Failed to load remote SDK: ${url}`, e)
        resolve(currentRemoteSdk)
      }
    }
  })
}

export async function initChat(payload: {
  proxyHost: string
  touchpointId: string
  region: Region
  languageCode: string
  enableEmoji: boolean
  enableAttachments: boolean
  enableValidation: boolean
  enableAutoOpen: boolean
  enableResponsiveLayout: boolean
  enablePointMoveTriggerButton: boolean
  enableUserInput: boolean
  context: string
  styles: Theme
  onEvent: (event: string) => void
}): Promise<SDK> {
  const {
    proxyHost,
    touchpointId,
    region,
    languageCode,
    enableEmoji,
    enableAttachments,
    enableValidation,
    enableAutoOpen,
    enableResponsiveLayout,
    enablePointMoveTriggerButton,
    enableUserInput,
    context,
    styles,
    onEvent
  } = payload
  let CurrentSdk: typeof TalkdeskChatSDK | null = null
  if (import.meta.env.DEV) {
    CurrentSdk = (await import('@td-org/chat-widget-sdk')).default
  } else {
    const remoteModule = proxyHost
      ? `https://${proxyHost}${PROXY_URI.tdChatSdk}/v2/talkdeskchatsdk.js`
      : `${import.meta.env.VITE_CDN_BASE}/v2/talkdeskchatsdk.js`
    CurrentSdk = await loadRemoteSdk(remoteModule)
  }
  if (!CurrentSdk) {
    throw new Error('Failed to load TalkdeskChatSDK')
  }
  const chat = CurrentSdk('injector', {
    touchpointId,
    accountId: '',
    region,
    autoOpen: enableAutoOpen,
    proxyHost
  })

  const promise = chat.init({
    enableEmoji,
    enableAttachments,
    enableValidation,
    enableResponsiveLayout,
    enablePointMoveTriggerButton,
    enableUserInput,
    languageCode,
    styles
  })

  chat.onOpenWebchat = () => {
    onEvent('Chat Widget opened')
  }

  chat.onCloseWebchat = () => {
    onEvent('Chat Widget closed')
  }

  chat.onConversationStart = () => {
    onEvent('Conversation started')
  }

  try {
    const contextValue = JSON.parse(context)
    chat.setContextParam(contextValue)
  } catch (e) {
    console.error('Invalid context value: ', e)
  }

  await promise

  onEvent('Chat Widget initialized')

  return chat
}

export async function initPreview(payload: {
  touchpointId: string
  region: Region
  enableEmoji: boolean
  enableAttachments: boolean
  enableValidation: boolean
  enableResponsiveLayout: boolean
  enableUserInput: boolean
  context: string
  previewConfig: string
  styles: Theme
  onEvent: (event: string) => void
}): Promise<PreviewSDK> {
  const {
    enableEmoji,
    enableAttachments,
    enableValidation,
    enableResponsiveLayout,
    enableUserInput,
    styles,
    previewConfig
  } = payload
  const TalkdeskChatSDK = await import('@td-org/chat-widget-sdk/preview')
  const chat = TalkdeskChatSDK.default('preview')

  chat.init({
    enableEmoji,
    enableAttachments,
    enableValidation,
    enableResponsiveLayout,
    enableUserInput,
    styles
  })

  chat.updateConfig(JSON.parse(previewConfig))

  return chat
}
