const Regions = ['td-us-1', 'td-ca-1', 'td-eu-1', 'td-usfed-1'] as const

export type Region = (typeof Regions)[number]

export function isValidRegion(value: string): value is Region {
  return getRegions().includes(value as Region)
}

export function getRegion(value: string): Region | undefined {
  if (isValidRegion(value)) {
    return value
  }
}

export function getRegions(): Region[] {
  const mode = import.meta.env.MODE
  switch (mode) {
    case 'qa':
      return ['td-us-1', 'td-ca-1']
    case 'production':
      return ['td-us-1', 'td-ca-1', 'td-eu-1', 'td-usfed-1']
    case 'development':
    case 'test':
    case 'stg':
    default:
      return ['td-us-1']
  }
}
