import { useMemo, useState } from 'react'
import clsx from 'clsx'

import { type Theme } from '@td-org/chat-widget-shared'
import { OPTIONS_KEYS, removeEmptyKeys } from '@td-org/chat-widget-shared'

import { buildSnippetCode } from '../../utils/buildSnippetCode.ts'
import { convertSearchString, getChecked, getValue } from '../../utils/helper'
import { getRegions, type Region } from '../../utils/region'
import Field from '../Field'
import JsonEditor from '../JsonEditor'
import Output from '../Output'
import RadioGroup from '../RadioGroup'
import Section from '../Section'
import SnippetCode from '../SnippetCode'
import StyleInput from '../StyleInput'

import classes from './ConfigurationForm.module.scss'

type ThemeKey = keyof Theme

type ConfigurationFormProps = {
  proxyHost: string
  touchpointId?: string
  region: Region
  lang: string
  enableEmoji: boolean
  enableAttachments: boolean
  enableValidation: boolean
  enableAutoOpen: boolean
  enableResponsiveLayout: boolean
  enablePointMoveTriggerButton: boolean
  enableUserInput: boolean
  enablePreview: boolean
  defaultTheme: Theme
  events: Array<{ name: string; timestamp: Date }>
  onClearEvents: () => void
  defaultContext: string
  defaultPreviewConfig: string
  onUpdate?: (
    data: {
      proxyHost: string
      touchpointId: string
      region: Region
      lang: string
      enableEmoji: boolean
      enableAttachments: boolean
      enableValidation: boolean
      enableAutoOpen: boolean
      enableResponsiveLayout: boolean
      enablePointMoveTriggerButton: boolean
      enableUserInput: boolean
      enablePreview: boolean
      context: string
      previewConfig: string
      styles: Partial<Theme>
    },
    isConfigs: boolean
  ) => void
  onUpdatePreviewConfig?: (config: string) => void
  onDestroy?: () => void
  onUpdateContext?: (context: string) => void
}

const availableRegions = getRegions().map((r) => ({ text: r, value: r }))

const languages = [
  { value: '', label: 'Using browser setting selected' },
  { value: 'en-US', label: '' },
  { value: 'es-ES', label: '' },
  { value: 'de-DE', label: '' },
  { value: 'fr-FR', label: '' },
  { value: 'it-IT', label: '' },
  { value: 'pt-BR', label: '' },
  { value: 'pt-PT', label: '' },
  { value: 'ar-SA', label: '' },
  { value: 'ar-EG', label: '' }
]

export function ConfigurationForm(props: ConfigurationFormProps) {
  const {
    defaultTheme,
    defaultContext,
    defaultPreviewConfig,
    events,
    onClearEvents,
    onUpdate,
    onUpdatePreviewConfig,
    onDestroy,
    onUpdateContext
  } = props

  const [search, setSearch] = useState<string>('')
  const themeKeys = useMemo(
    () =>
      Object.keys(defaultTheme).filter((key) => {
        return convertSearchString(search).some((s) => key.toLowerCase().includes(s.toLowerCase()))
      }) as ThemeKey[],
    [defaultTheme, search]
  )

  const [proxyHost, setProxyHost] = useState(props.proxyHost || '')
  const [touchpointId, setTouchpointId] = useState<string>(props.touchpointId || '')
  const [region, setRegion] = useState<Region>(props.region || 'td-us-1')
  const [lang, setLang] = useState<string>(props.lang || '')
  const [enableEmoji, setEnableEmoji] = useState<boolean>(props.enableEmoji)
  const [enableAttachments, setEnableAttachments] = useState<boolean>(props.enableAttachments)
  const [enableValidation, setEnableValidation] = useState<boolean>(props.enableValidation)
  const [enableAutoOpen, setEnableAutoOpen] = useState<boolean>(props.enableAutoOpen)
  const [enableResponsiveLayout, setEnableResponsiveLayout] = useState<boolean>(props.enableResponsiveLayout)
  const [enablePointMoveTriggerButton, setEnablePointMoveTriggerButton] = useState<boolean>(
    props.enablePointMoveTriggerButton
  )
  const [enableUserInput, setEnableUserInput] = useState<boolean>(props.enableUserInput)
  const [enablePreview, setEnablePreview] = useState<boolean>(props.enablePreview)
  const [context, setContext] = useState(defaultContext)
  const [previewConfig, setPreviewConfig] = useState(defaultPreviewConfig)
  const [styles, setStyles] = useState<Partial<Theme>>(themeKeys.reduce((acc, key) => ({ ...acc, [key]: '' }), {}))
  const [isSticky, setIsSticky] = useState<boolean>(false)

  const appProps = useMemo(
    () => ({
      touchpointId,
      region,
      accountId: '',
      proxyHost
    }),
    [touchpointId, region, proxyHost]
  )

  const appConfig = useMemo(
    () => ({
      enableEmoji,
      enableAttachments,
      enableValidation,
      enableAutoOpen,
      enableResponsiveLayout,
      enablePointMoveTriggerButton,
      enableUserInput,
      languageCode: lang,
      styles: removeEmptyKeys({ ...styles }, { removeEmptyString: true })
    }),
    [
      enableEmoji,
      enableAttachments,
      enableValidation,
      enableAutoOpen,
      enableResponsiveLayout,
      enablePointMoveTriggerButton,
      enableUserInput,
      lang,
      styles
    ]
  )

  const handleCopyClick = (shouldAlert = false) => {
    const code = buildSnippetCode(appProps, appConfig)
    if (navigator.clipboard !== undefined) {
      navigator.clipboard
        .writeText(code)
        .then(() => {
          if (shouldAlert) {
            alert('Snippet code copied to clipboard.')
          }
        })
        .catch((err) => {
          console.error('Failed to copy: ', err)
        })
    } else {
      console.error('Your browser does not support clipboard feature, please copy the snippet code manually.')
    }
  }

  const handleUpdateClick = (isAppConfigs = false) => {
    if (onUpdate) {
      onUpdate(
        {
          proxyHost,
          touchpointId,
          region,
          enablePreview,
          context,
          previewConfig,
          lang: appConfig.languageCode,
          enableEmoji: appConfig.enableEmoji,
          enableAttachments: appConfig.enableAttachments,
          enableValidation: appConfig.enableValidation,
          enableAutoOpen: appConfig.enableAutoOpen,
          enableResponsiveLayout: appConfig.enableResponsiveLayout,
          enablePointMoveTriggerButton: appConfig.enablePointMoveTriggerButton,
          enableUserInput: appConfig.enableUserInput,
          styles: appConfig.styles
        },
        isAppConfigs
      )
      handleCopyClick(false)
    }
  }

  const handleDestroyClick = () => {
    if (onDestroy) {
      onDestroy()
    }
  }

  const handleStylesChange = (key: ThemeKey, value: string) => {
    setStyles((prev) => {
      return {
        ...prev,
        [key]: ([...OPTIONS_KEYS] as string[]).includes(key) ? value : value.trim()
      }
    })
  }

  const handleUpdatePreviewConfig = () => {
    onUpdatePreviewConfig?.(previewConfig)
  }

  return (
    <div className={classes.config}>
      <Section title='Widget properties'>
        <Field label='Touchpoint ID'>
          <input id='TouchpointID' value={touchpointId} onChange={(e) => setTouchpointId(getValue(e).trim())}></input>
        </Field>
        <Field label='ProxyHost'>
          <input id='ProxyHost' value={proxyHost} onChange={(e) => setProxyHost(getValue(e).trim())} />
        </Field>
        <Field label='Language'>
          <select value={lang} onChange={(e) => setLang(e.target.value)}>
            {languages.map((lang, index) => (
              <option key={index} value={lang.value}>
                {lang.value ? `${lang.label} (${lang.value})` : lang.label}
              </option>
            ))}
          </select>
        </Field>
        <Field label='Region'>
          <RadioGroup
            name='Region'
            value={region}
            items={availableRegions}
            onChange={(value) => setRegion(value as Region)}
          />
        </Field>
        <Field label='Enable emoji'>
          <input
            id='EnableEmoji'
            type='checkbox'
            checked={enableEmoji}
            onChange={(e) => setEnableEmoji(getChecked(e))}
          ></input>
        </Field>
        <Field label='Enable attachments'>
          <input
            id='EnableAttachments'
            type='checkbox'
            checked={enableAttachments}
            onChange={(e) => setEnableAttachments(getChecked(e))}
          ></input>
        </Field>
        <Field label='Enable validation'>
          <input
            id='EnableValidation'
            type='checkbox'
            checked={enableValidation}
            onChange={(e) => setEnableValidation(getChecked(e))}
          ></input>
        </Field>
        <Field label='Enable AutoOpen'>
          <input
            id='EnableAutoOpen'
            type='checkbox'
            checked={enableAutoOpen}
            onChange={(e) => setEnableAutoOpen(getChecked(e))}
          />
        </Field>
        <Field label='Enable Responsive Layout'>
          <input
            id='enableResponsiveLayout'
            type='checkbox'
            checked={enableResponsiveLayout}
            onChange={(e) => setEnableResponsiveLayout(getChecked(e))}
          ></input>
        </Field>
        <Field label='Enable Move Trigger Button'>
          <input
            id='enablePointMoveTriggerButton'
            type='checkbox'
            checked={enablePointMoveTriggerButton}
            onChange={(e) => setEnablePointMoveTriggerButton(getChecked(e))}
          ></input>
        </Field>
        <Field label='Enable User input'>
          <input
            id='enableUserInput'
            type='checkbox'
            checked={enableUserInput}
            onChange={(e) => setEnableUserInput(getChecked(e))}
          ></input>
        </Field>
        <Field label='Enable Preview Mode'>
          <input
            id='enableWidgetPreviewMode'
            type='checkbox'
            checked={enablePreview}
            onChange={(e) => setEnablePreview(getChecked(e))}
          ></input>
        </Field>
      </Section>

      <Section title='Widget preview config' defaultExpand={enablePreview}>
        <button onClick={handleUpdatePreviewConfig}>Update preview config</button>
        <JsonEditor value={previewConfig} onChange={setPreviewConfig} />
      </Section>

      <Section title='Widget styles' defaultExpand={false} onStickyTop={setIsSticky}>
        <input
          className={clsx([classes.search, { [classes.searchFixed]: isSticky }])}
          id='search'
          type='search'
          placeholder='Search for styles based on keys. Multiple keys can be separated by ",".'
          value={search}
          onChange={(e) => setSearch(getValue(e))}
          enterKeyHint='search'
        ></input>
        {themeKeys.map((key) => (
          <Field label={key} key={key} search={search}>
            <StyleInput
              styleKey={key}
              value={styles[key] || ''}
              defaultValue={defaultTheme[key]}
              onChange={handleStylesChange}
            />
          </Field>
        ))}
        {themeKeys.length === 0 && <span>No result</span>}
      </Section>
      <Section title='Widget context' defaultExpand={false}>
        <button onClick={() => onUpdateContext?.(context)}>Update context</button>
        <JsonEditor value={context} onChange={setContext} />
      </Section>
      <Section title='Widget events' defaultExpand={false}>
        <Output events={events} onClearEvents={onClearEvents} />
      </Section>
      <Section title='Widget snippet code' defaultExpand={false}>
        <SnippetCode appProps={appProps} appConfig={appConfig} />
      </Section>
      <div className={classes.actions}>
        <button className={classes.update} onClick={() => handleUpdateClick(false)}>
          Update
        </button>
        <button className={classes.update} onClick={() => handleUpdateClick(true)}>
          Update configs
        </button>
        <button className={classes.copy} onClick={() => handleCopyClick(true)}>
          Copy snippet code
        </button>
        <button className={classes.destroy} onClick={handleDestroyClick}>
          Destroy
        </button>
      </div>
    </div>
  )
}
