import React from 'react'

import { type AppConfig, type AppProps } from '@td-org/chat-widget-shared'

import { buildSnippetCode } from '../../utils/buildSnippetCode.ts'

import styles from './SnippetCode.module.scss'

export function SnippetCode(props: { appProps: AppProps; appConfig: AppConfig }): React.ReactNode {
  const { appProps, appConfig } = props
  return <pre className={styles.code}>{buildSnippetCode(appProps, appConfig)}</pre>
}
